<template>
  <div>
    <SectionTitle :title="sectiontitle" />
    <div class="content-component content-card">
      <div class="card-inner">
        <div class="card-detail"></div>
        <SearchFormCombine :bar="bar1" @btn-click-event="handleRecordQuery" />
        <TabulatorTable
          :tableId="table1Id"
          :columns="table1Columns"
          :tableData="table1Data"
          checkId1="samplingrecord-table1-check1"
          checkId2="samplingrecord-table1-check2"
        />
      </div>
    </div>
  </div>
</template>
<script>
import callApi from "../helperFunctions/callApi";
export default {
  name: "SamplingRecord",
  methods: {
    async handleRecordQuery({ formData, allEmpty }) {
      if (!formData["agent_code"]) {
        //modal popup
        this.$bvModal.show("bv-modal-example");
      } else {
        console.log("api");
        const domain = this.$store.state.domain;
        const url = domain + "/newSamplingRecord/executeMission02";
        const params = {
          agent_code: formData["agent_code"],
        };
        const headers = {
          "Content-Type": "application/x-www-form-urlencoded",
        };
        const { res } = callApi();
        const { data } = await res(url, params, headers);
        this.table1Data = data.data;
      }
    },
  },
  data() {
    return {
      table1Id: "samplingTable1",
      table1Columns: [
        { title: "	", field: "r_policy_no" },
        { title: "	", field: "r_po_sts_desc" },
        { title: "", field: "r_app_input_date" },
        { title: "", field: "r_medical_desc" },
      ],
      table1Data: [],
      sectiontitle: "",
      bar1: [
        {
          inputType: "textfield",
          inputName: "ID",
          placeholder: "ID",
          id: "agent_code",
          inputVal: "",
        },
        {
          inputType: "textfield",
          inputName: "",
          placeholder: "",
          id: "agent_name",
          inputVal: "",
        },
        {
          inputType: "dropdown",
          inputName: "(Y/N)",
          options: [
            { text: "Y", value: "Y" },
            { text: "N", value: "N" },
          ],
          id: "immune",
          inputVal: "Y",
        },
      ],
    };
  },
};
</script>
