//52--
<template>
  <div>
    <SectionTitle :title="sectiontitle" />
    <div class="content-component content-card">
      <div class="card-inner">
        <div class="card-detail">
          <SearchFormCombine :bar="bar1" @btn-click-event="clickHandler" />
          <Table :colNum="3" :table="table" />
          <TabulatorTable
            :tableId="Table1Id"
            :columns="Table1Columns"
            :tableData="Table1Data"
            checkId1="mediapayment-table1-check1"
            checkId2="mediapayment-table1-check2"
          />
          <Accordion id="acc1" title="ATM">
            <TabulatorTable
              :tableId="Table2Id"
              :columns="Table2Columns"
              :tableData="Table2Data"
              checkId1="mediapayment-acc1table2-check1"
              checkId2="mediapayment-acc1table2-check2"
            />
          </Accordion>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import callApi from "../helperFunctions/callApi";
import { populateTable } from "../helperFunctions/commonVue.js";
export default {
  name: "MediaPayment",
  mounted() {
    this.watchApiCall();
  },
  computed: {
    insPolicyNumber() {
      return this.$store.state.insPolicyNumber;
    },
  },
  watch: {
    insPolicyNumber(newVal, oldVal) {
      //call api to set table1Data
      this.watchApiCall(); //table
      // localStorage.policy_no=newVal;
      console.log(`newVal: ${newVal} \n oldVal: ${oldVal}`);
    },
    immediate: true,
  },
  methods: {
    async watchApiCall() {
      const domain = this.$store.state.domain;
      const url = domain + "/premPostal/executeMission01";
      // const token = this.$store.state.token;
      const params = {
        policy_no: this.insPolicyNumber,
      };
      const headers = {
        "Content-Type": "application/x-www-form-urlencoded",
        //Authorization: "bearer " + token,
      };
      const { res } = callApi();
      const { data } = await res(url, params, headers);
      this.paymentTableData=data.data[0]
      populateTable(data.data[0], this.table);
    },
    clickHandler({ formData, allEmpty }) {
      this.paymentType=formData.payment;
      if (allEmpty) {
        this.$bvModal.show("bv-modal-example");
      } else {
        //  call api
        if (this.paymentType === "0") {
          this.paymentTableApiCall(); //tabulator
        } else {
          this.atmTableApiCall(); //ATM
        }
      }
    },
    async paymentTableApiCall() {
      this.Table1Data=[];
       const domain = this.$store.state.domain;
      const url = domain + "/premPostal/executeMission02";

      const params = {
        currency: this.paymentTableData.currency,
        policy_no: this.insPolicyNumber,
      };

      const headers = {
        "Content-Type": "application/x-www-form-urlencoded",
        //Authorization: "bearer " + token,
      };
      const { res } = callApi();
      const { data, status } = await res(url, params, headers);
      let table1Data=data.data;
      this.Table1Data=table1Data;
    },
    async atmTableApiCall(){
      this.Table2Data=[];
       const domain = this.$store.state.domain;
      const url = domain + "/premPostal/executeMission03";

      const params = {
        type:  this.paymentType,
        policy_no: this.insPolicyNumber,
      };

      const headers = {
        "Content-Type": "application/x-www-form-urlencoded",
        //Authorization: "bearer " + token,
      };
      const { res } = callApi();
      const { data, status } = await res(url, params, headers);
      let table2Data=data.data;
      this.Table2Data=table2Data
    },
  },

  data() {
    return {
      paymentType:null,
      paymentTableData: null,
      sectiontitle: "",
      bar1: [
        {
          inputType: "dropdown",
          inputName: "",
          options: [
            { text: "", value: "0" },
            { text: "ATM", value: "1" },
            { text: "", value: "2" },
            { text: "", value: "3" },
          ],
          id: "payment",
          inputVal: "0",
        },
      ],
      table: [
        { key: "", keyId: "policy_no", value: "" },
        { key: "", keyId: "owner_name", value: "" },
        { key: "", keyId: "po_issue_date", value: "" },
        { key: "", keyId: "po_sts_desc", value: "" },
        { key: "", keyId: "insured_name", value: "" },
        { key: "", keyId: "paid_to_date", value: "" },
        { key: "", keyId: "method_desc", value: "" },
        { key: "", keyId: "modx_desc", value: "" },
        { key: "", keyId: "gross_mode_prem", value: "" },
        { key: "/", keyId: "prem_susp", value: "" },
        { key: "", keyId: 11, value: "" },
        { key: "", keyId: "div_amt", value: "" },
      ],
      Table1Id: "homeTable1",
      Table1Columns: [
        { title: "", field: "seq" },
        { title: "", field: "source_desc" },
        { title: "", field: "transfer_date" },
        { title: "	", field: "transfer_amt" },
        { title: "", field: "bill_date_from" },
        { title: "	", field: "mode_prem" },
        { title: "//", field: "store_name" },
        { title: "	/", field: "store_other" },
        { title: "", field: "tran_date" },
        { title: "", field: "tran_sts" },
      ],
      Table1Data: [],
      Table2Id: "homeTable2",
      Table2Columns: [
        { title: "	", field: "transfer_bank" },
        { title: "", field: "transfer_account" },
        { title: "", field: "transfer_time" },
        { title: "ATM", field: "atm_code" },
        { title: "", field: "atm_seq" },
        { title: "", field: "atm_fee" },
      ],
      Table2Data: [],
    };
  },
};
</script>
