<template>
  <div>
    <SectionTitle :title="sectiontitle" />
    <div class="content-component content-card">
      <div class="card-inner">
        <div class="card-detail">
          <Accordion id="acc1" title="">
            <Tags :tags="tags" />
            <TabulatorTable
              :tableId="Table1Id"
              :columns="Table1Columns"
              :tableData="Table1Data"
              checkId1="unpaiquery-acc1table1-check1"
              checkId2="unpaiquery-acc1table1-check2"
              @transmit-tabulator-data="handleTabulatorData"
            />
          </Accordion>
          <Accordion id="acc2" title="">
            <TabulatorTable
              :tableId="Table2Id"
              :columns="Table2Columns"
              :tableData="Table2Data"
              checkId1="unpaiquery-acc2table2-check1"
              checkId2="unpaiquery-acc2table2-check2"
            />
            <TabulatorTable
              :tableId="Table3Id"
              :columns="Table3Columns"
              :tableData="Table3Data"
              checkId1="unpaiquery-acc2table3-check1"
              checkId2="unpaiquery-acc2table3-check2"
            />
          </Accordion>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import callApi from "../helperFunctions/callApi";
export default {
  name: "UnpaidQuery",
  mounted() {
    this.unpaidSearchApi();
  },
  computed: {
    applicantId() {
      return this.$store.state.applicant;
    },
  },
  methods: {
    async unpaidSearchApi() {
      const domain = this.$store.state.domain;
      const url = domain + "/shouldPayNoPayData/executeMission01";
      // const token = this.$store.state.token;
      const params = {
        o1_id: this.applicantId,
      };
      const headers = {
        "Content-Type": "application/x-www-form-urlencoded",
        //Authorization: "bearer " + token,
      };
      const { res } = callApi();
      const { data } = await res(url, params, headers);
      this.tags[0].detail = this.applicantId;
      let table1Data = data.data;
      this.Table1Data = table1Data;
    },
    async handleTabulatorData(rowValue) {
      this.Table2Data = [];
      this.Table3Data = [];
      console.log(rowValue);
      const domain = this.$store.state.domain;
      const url = domain + "/shouldPayNoPayData/executeMission02";
      // const token = this.$store.state.token;
      const params = {
        pshpPolicy_no: rowValue.policy_no,
      };
      const headers = {
        "Content-Type": "application/x-www-form-urlencoded",
        //Authorization: "bearer " + token,
      };
      const { res } = callApi();
      const { data } = await res(url, params, headers);
      let tableResult = data.data;
      this.Table2Data = tableResult;
      this.Table3Data = tableResult;
    },
  },
  data() {
    return {
      sectiontitle: "",
      tags: [{ title: "", detail: "" }],
      Table1Id: "homeTable1",
      Table1Columns: [
        { title: "", field: "policy_no" },
        { title: "", field: "currency" },
        { title: "", field: "po_sts_code" },
        { title: "", field: "o1_name" },
        { title: "", field: "i1_name" },
        { title: "", field: "survialBeneficiaryNameString	" },
        { title: "", field: "maturedBeneficiaryNameString" },
        { title: "", field: "deathBeneficiaryNameString" },
      ],
      Table1Data: [],
      Table2Id: "homeTable2",
      Table2Columns: [
        { title: "", field: "tran_date_o" },
        { title: "", field: "function_code" },
        { title: "", field: "pshp_indDesc" },
        { title: "", field: "disb_no_o" },
        { title: "", field: "names" },
        { title: "ID", field: "client_id" },
        { title: "", field: "payee" },
        { title: "", field: "dept_code" },
      ],
      Table2Data: [],
      Table3Id: "homeTable3",
      Table3Columns: [
        { title: "", field: "tran_date" },
        { title: "", field: "disb_no" },
        {
          title: "",
          field: "",
          formatter: function(cell) {
            let data = cell.getRow().getData();
            return data.disb_sts_code + " " + data.disb_sts_desc;
          },
        },
        {
          title: "",
          field: "",
          formatter: function(cell) {
            let data = cell.getRow().getData();
            return data.pshp_sts_code + " " + data.pshp_sts_Desc;
          },
        },
        { title: "", field: "process_date" },
        { title: "", field: "fapply_dept_code" },
        { title: "", field: "apply_userName" },
        { title: "", field: "apply_date" },
        { title: "", field: "approve_userName" },
        { title: "", field: "approve_date" },
      ],
      Table3Data: [],
    };
  },
};
</script>
